import React from "react";
import "../styles/CertPage.css";
import data from '../data.json'
import Project from "./Project";
import caesarsCipher from "../images/2/caesars-cipher.jpg";
import cashRegister from "../images/2/cash-register.jpg";
import palindromeChecker from "../images/2/palindrome-checker.jpg";
import romanNumeralConverter from "../images/2/roman-numeral-converter.jpg";
import telephoneNumberValidator from "../images/2/telephone-number-validator.jpg";

function CertTwo(props) {
  let i = -1;
  const images = [caesarsCipher, cashRegister, palindromeChecker, romanNumeralConverter, telephoneNumberValidator]
  const projects = data.certTwo.map((p) => {
    i++
    return <Project 
    area={`item${i}`}
    id={p.id}
    live={p.live}
    repo={p.repo}
    image={images[i]}
    alt={p.alt}
    title={p.title}
    />
  })
  return (
    <div id='page'>
      {props.navbar}
      <br /><br />
      <div className="container">
        {props.header2}

        <div>
            {projects}
        </div>
      </div>
    </div>
  );
}

export default CertTwo;

