import React from 'react'
import '../styles/SideProjects.css'
import data from '../data.json'
import Project from "./Project";
import medicareMore from '../images/sideProjects/medicareMore.JPG'

export default function SideProjects(props) {
    let i = -1;
    const images = [medicareMore]
    const projects = data.sideProjects.map((p) => {
        i++
        return <Project
        area={`item${i}`}
        id={p.id}
        live={p.live}
        repo={p.repo}
        image={images[i]}
        alt={p.alt}
        title={p.title}
        />
      })
    return (
        <div className='container'>
            {props.navbar}
            <br />
            <br />
            <h1 className='title'>Side Projects</h1>
            {projects}
        </div>
    )
}