import React from "react";
import "../styles/CertPage.css";
import data from '../data.json'
import Project from "./Project";
import tribute from "../images/1/tribute.jpg";
import survey from "../images/1/survey.jpg";
import productLanding from "../images/1/product-landing.jpg";
import docsPage from "../images/1/docs-page.jpg";
import portfolio from "../images/1/portfolio.jpg";

function CertOne(props) {
  let i = -1;
  const images = [tribute, survey, productLanding, docsPage, portfolio]
  const projects = data.certOne.map((p) => {
    i++
    return <Project
    area={`item${i}`}
    id={p.id}
    live={p.live}
    repo={p.repo}
    image={images[i]}
    alt={p.alt}
    title={p.title}
    />
  })
  return (
    <div id='page'>
      {props.navbar}
      <br /><br />
      <div className="container">
        {props.header1}

        <div>
          {projects}
        </div>
      </div>
    </div>
  );
}

export default CertOne;
